<template>
    <div class="vue-upload-web">
    </div>
</template>


<script>
  import WebUploader from './webuploader';
  import swgimg from './Uploader.swf';

  export default {
    name: 'vue-upload-web',
    props: {
      accept: {
        type: Object,
        default: null,
      },
      // 
      url: {
        type: String,
        default: '',
      },
      //  100
      fileNumLimit: {
        type: Number,
        default: 100,
      },
      //  2M
      fileSingleSizeLimit: {
        type: Number,
        default: 2048000,
      },
      // token
      formData: {
        type: Object,
        default: null,
      },
      keyGenerator: {
        type: Function,
        default(file) {
          const currentTime = new Date().getTime();
          const key = `${currentTime}.${file.name}`;
          return key;
        },
      },
      multiple: {
        type: Boolean,
        default: false,
      },
      // ID
      uploadButton: {
        type: String,
        default: '',
      },
    },
    data() {
      return {
      };
    },
    mounted() {
      this.initWebUpload();
    },
    methods: {
      initWebUpload() {
        const self = this;
        const uploader = WebUploader.create({
          auto: true, // 
          swf: swgimg, // '/upload/Uploader.swf', // swf
          server: self.url,  // 
          pick: {
            id: self.uploadButton,
            multiple: self.multiple,   //  false
            label: '',
          }, // 

          // 
          accept: self.accept,
          thread: 1,
          fileNumLimit: self.fileNumLimit, // 
          fileSingleSizeLimit: self.fileSingleSizeLimit, // 
          formData: self.formData,  // 
          duplicate: true,  // 
        });
        // 
        uploader.on('fileQueued', (file) => {
          self.$emit('before', file);
        });
        uploader.on('uploadStart', (file) => {
          uploader.options.formData.key = self.keyGenerator(file);
        });

        // 
        uploader.on('uploadProgress', (file, percentage) => {
          self.$emit('progress', file, percentage);
        });
        uploader.on('uploadSuccess', (file, response) => {
          self.$emit('success', file, response);
        });

        uploader.on('error', (type) => {
          let errorMessage = '';
          if (type === 'F_EXCEED_SIZE') {
            errorMessage = `${self.fileSingleSizeLimit / (1024 * 1000)}M`;
          } else if (type === 'Q_EXCEED_NUM_LIMIT') {
            errorMessage = '';
          } else {
            errorMessage = `${type}`;
          }
          self.$emit('error', errorMessage);
        });

        uploader.on('uploadComplete', (file, response) => {
          self.$emit('complete', file, response);
        });
      },
    },
  };
</script>

<style>
  .webuploader-container {
    position: relative;
  }
  .webuploader-element-invisible {
    position: absolute !important;
    clip: rect(1px 1px 1px 1px); /* IE6, IE7 */
    clip: rect(1px,1px,1px,1px);
  }
  .webuploader-pick {
    position: relative;
    display: inline-block;
    cursor: pointer;
    background: #00b7ee;
    padding: 10px 15px;
    color: #fff;
    text-align: center;
    border-radius: 3px;
    overflow: hidden;
  }
  .webuploader-pick-hover {
    background: #00a2d4;
  }

  .webuploader-pick-disable {
    opacity: 0.6;
    pointer-events:none;
  }
</style>



